export default function PostRead(){
  const history = useHistory()
  const [isOpen, setIsOpen] = useState(false)
  //  
  const [edit, setEdit] = useState(false)
  //  
  const [removePost, setRemovePost] = useState(false)

  //  
  const editPost = () => {
    setEdit(!isOpen)
    history.push()
  }
  //  
  const deletePost = () => {
    setRemovePost(!isOpen)
    history.push()
  }
  const editModalYes = () => {
    console.log('');
    setEdit(false)
  }
  const removeModalYes = () => {
    console.log('')
    setRemovePost(false)
  }
  const modalNoButtonHandler = () => {
    setRemovePost(false)
    setEdit(false)
  }
  const modalCloseButtonHandler = () => {
    setRemovePost(false)
    setEdit(false)
  }

  const bookmarkHandler = (e) => {
    console.log(e.currentTarget);

  }

  // top button
  const [ScrollY, setScrollY] = useState(0);
  const [btnStatus, setBtnStatus] = useState(false);  //  

  // console.log(window.pageYOffset)
  // console.log(btnStatus)

  const handleFollow = () => {
    setScrollY(window.scrollY)
    if(ScrollY > 200){  // 200   
      setBtnStatus(true)
    }else{  // 200   
      setBtnStatus(false)
    }
  }

  //   
  const scrollToTop = () => {
    // e.preventDefault() //  
    window.scrollTo({top: 0, behavior: 'smooth'}) //  
    setScrollY(0);  //   0 
    setBtnStatus(false); //   
  }

  useEffect(() => {
    window.addEventListener('scroll', handleFollow)
    return () => {
      window.removeEventListener('scroll', handleFollow)
    }
  })

  return (
    <Outer>
      <TopButton>
        {
          btnStatus?
          <img 
            src={`${process.env.PUBLIC_URL}img/scroll-up-2.png`} alt="top"
            onClick={scrollToTop}
          /> : null
        }
      </TopButton>
      <GoBackButton/>
      <PostHeader>
        <Title>
          <BookmarkIcon>
            <FontAwesomeIcon icon={faHeart} size="2x" className="heart" onClick={bookmarkHandler}/>
            
          </BookmarkIcon>
          <span>{'  '}</span>
        </Title>

        <Profile>
          <div className="profileInfo">
            <ProfileImg src="img/user-img.png"/>
            <span className="nickName">{''}</span>
            <span className="date">{'2021. 10.19. 15:24'}</span>
          </div>
          <p className="location">{'  '}</p>
        </Profile>
      </PostHeader>
      <PostImg src={`${process.env.PUBLIC_URL}img/sky.png`} alt="weather"/>

      <WeatherInfo>
          <WeatherIcon src={`${process.env.PUBLIC_URL}img/icons-write/sunny.png`} alt=""/>
          <WeatherIcon src={`${process.env.PUBLIC_URL}img/icons-write/breezy.png`} alt=""/>
          <WeatherIcon src={`${process.env.PUBLIC_URL}img/icons-write/hot.png`} alt=""/>
      </WeatherInfo>
      
      {/*   ,   */}
      <p className="todayCodi"> </p>
      <TodayCodi>
          <FontAwesomeIcon icon={faTshirt} color="purple"/>
          <FontAwesomeIcon icon={faTshirt} color="pink"/>
      </TodayCodi>

      <Post>
        <p>
                 .    ,    .       ,  .       .     ?     .     .
        </p>
      </Post>

      <Buttons>
        <button className="button button1" value="delete" onClick={deletePost}></button>
        {removePost === false ? null : (
          <ModalConfirm
            yesHandler={removeModalYes}
            noHandler={modalNoButtonHandler}
            closeHandler={modalCloseButtonHandler}
          ></ModalConfirm>
          )}
        <button className="button button2" onClick={editPost}></button>
        {edit === false ? null : (
          <ModalConfirm
            yesHandler={editModalYes}
            noHandler={modalNoButtonHandler}
            closeHandler={modalCloseButtonHandler}
          ></ModalConfirm>
        )}
      </Buttons>

      {/* <TopButton>
          <img src={`${process.env.PUBLIC_URL}img/scroll-up-2.png`} alt="top"></img>
      </TopButton> */}

    </Outer>
  )
}